* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  /* Fonts */
  --font-family: "Montserrat", serif;
  --font-size: 16px;

  /* Colors */
  --black: #000000;
  --white: #ffffff;
  --error-color: #cc0000;
  --primary-color: #0066cc;
  --secondary-color: #f5f7fb;
  --border-color: #d9d9d9;
  --placeholder-color: rgba(0, 0, 0, 0.6);

  /* Shadow */
  --shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1),
    0 5px 20px 0 rgba(29, 33, 38, 0.03);
}

html,
body {
  font-size: var(--font-size);
}

[data-theme="1"] {
  --color: #000000;
  --background: #ffffff !important;
}

[data-theme="2"] {
  --color: #000000;
  --background: #ddeeff !important;
}

[data-theme="3"] {
  --color: #ffffff;
  --background: #212a34 !important;
}

html {
  background-color: var(--background);
  color: var(--color);
}

body {
  font-family: var(--font-family);
  font-weight: 400;
}

.wrapper {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.error-message-span {
  color: var(--error-color);
  font-size: 0.8rem;
}

#hw5-page-junior {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

#hw10-button-start-loading {
  align-self: start;
}
